
.portfolio .nav-tabs {
  border: 0;
}

.portfolio .nav-link {
  border: 0;
  padding: 25px 20px;
  color: var(--color-secondary);
  box-shadow: 5px 5px 25px rgba(var(--color-secondary-rgb), 0.15);
  border-radius: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: 0s;
  cursor: pointer;
  height: 100%;
}

.portfolio .nav-link i {
  font-size: 32px;
  line-height: 0;
}

.portfolio .nav-link h4 {
  font-size: 20px;
  font-weight: 600;
  margin: 10px 0 0 0;
  color: var(--color-secondary);
}

.portfolio .nav-link:hover {
  color: var(--color-primary);
}

.portfolio .nav-link.active {
  transition: 0.3s;
  background: #34548c;
  border-color: var(--color-primary);
}

.portfolio .nav-link.active h5 {
  color: white;
}

.portfolio .nav-link.active i {
  color: white;
}

.portfolio .tab-content {
  margin-top: 30px;
}

.portfolio .tab-pane.active {
  animation: fadeIn 0.5s ease-out;
}

.portfolio .tab-pane h3 {
  font-weight: 600;
  font-size: 36px;
  color: var(--color-secondary);
}

.portfolio .tab-pane ul {
  list-style: none;
  padding: 0;
}

.portfolio .tab-pane ul li {
  padding-bottom: 10px;
}

.portfolio .tab-pane ul i {
  font-size: 24px;
  margin-right: 4px;
  color: #dc354b;
}

.portfolio .tab-pane p:last-child {
  margin-bottom: 0;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

